	module.exports = {"data":{"markdownRemark":{"html":"<h2>◆著作権情報について</h2>\n<ul>\n<li>\n<p>特別に記載されている場合を除き、当サイトの全コンテンツの著作権は、“Mc(まく)”、またはゲームサークル“danmaq”が保有します。</p>\n</li>\n<li>\n<p>当サイトの全て、または一部コンテンツの無断転載は著作権法上の例外(引用など)を除き禁止します。但し、<a href=\"/?attachment_id=16\">最上部のロゴ画像</a>はバナー兼用ですので、例外として無断転載を許可します。</p>\n</li>\n<li>\n<p>当サイトの記事を引用して、または当サイトのコンテンツを独自に改変して外部サイトで使用する場合、必ず出典(当サイト名、リンクURI)を付記してください。</p>\n</li>\n<li>\n<p>当サイト作品の二次創作、或いは三次創作に対し(18禁など含め)制限は原則として一切設けません。<em>(但し、Mc(まく)へ1部数押し売りすることを強く推奨します！)</em></p>\n</li>\n<li>\n<p>各種コンテンツ内で出てくるブランド名、会社名、製品名などは各メーカーの標章、または登録標章です。</p>\n<h2>◆免責事項について</h2>\n</li>\n<li>\n<p>当サイトおよびdanmaqのコンテンツは一切自己責任でご利用ください。</p>\n</li>\n<li>\n<p>もし万が一上記の利用が原因で何らかの危害を受けた場合、danmaqは一切その責任を負わないものとします。\nもしその他、ご不明な点がございましたら、danmaq代表のMc(まく)までお問い合わせください。</p>\n</li>\n</ul>","frontmatter":{"cover":null,"date":"2012-11-27T23:30:20+09:00","strDate":"2012/11/27","title":"著作権情報や免責事項について"}}},"pathContext":{"path":"/ja/blog/2012/11/27-legal/","langKey":"ja"}}

